// fonts
$font: 'Raleway', sans-serif;

// colors

$primary: #ee781e;
$secondary: #534c7c;
$gray: rgb(90, 90, 90);
$black: rgb(38, 38, 38);
$white: rgb(232, 232, 232);

// mixins

@mixin container {
    padding: 30px 0;
    width: 80%;
    margin: 0 auto;
}

@mixin heading {
    margin: 15px 0;
    font-size: 35px;
    font-weight: 600;
    text-align: center;
}

@mixin media($viewport) {
    @if $viewport == 'small' {
        @media all and (min-width: 0px) and (max-width: 650px) {
            @content;
        }
    } @else if $viewport == 'medium' {
        @media all and (min-width: 651px) and (max-width: 1024px) {
            @content;
        }
    } @else if $viewport == 'large' {
        @media all and (min-width: 1025px) and (max-width: 1500px) {
            @content;
        }
    } @else if $viewport == 'xl' {
        @media all and (min-width: 1501px) {
            @content;
        }
    }
}

html, body {
    height: 100%;
    margin: 0;
    font-family: $font;
    scroll-behavior: smooth;
}

// Components

// NAVBAR

.navbar {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    background-color: rgba(255, 255, 255, 0.85);
    padding: 0 20px;
    border-bottom: $secondary 6px solid;
    box-sizing: border-box;
    z-index: 1000;

    &__brand {
        height: 100%;
        display: flex;
        align-items: center;
    }

    &__logo {
        height: 80%;
    }

    &__content {
        padding-left: 20px;
    }

    &__title {
        margin: 0;
        font-size: 28px;
    }

    &__description {
        font-size: 14px;
    }

    &__menu {
        display: flex;
        justify-content: flex-end;
        margin: 0;
        padding: 0;
        color: $white;
        list-style-type: none;
    }

    &__item {
        padding: 0 15px;
        color: $secondary;
        font-family: $font;
        font-size: 18px;
        text-decoration: none;
        cursor: pointer;

        &:hover {
            color: $primary;
        }
    }

    // collapsed navbar

    &__nav-collapsed {
        display: none;
        align-items: center;
        height: 100%;
    }
    
    &__nav-collapsed-menu {
        display: none;
        position: absolute;
        top: 70px;
        left: 0;
        width: 100%;
        background-color: $secondary;
    }

    &__nav-collapsed--active &__nav-collapsed-menu {
        display: block;
    }

    &__nav-collapsed-item {
        font-size: 18px;
        color: $white;
        padding: 8px 16px 8px 16px;

        &:hover{
            color: $primary;
        }
    }
    
    &__nav-collapsed-container {
        background-color: transparent;
    }

    &__nav-collapsed-bar1,
    &__nav-collapsed-bar2,
    &__nav-collapsed-bar3 {
        width: 35px;
        height: 5px;
        background-color: $gray;
        margin: 6px 0;
        transition: 0.4s;
    }

    &__nav-collapsed--active &__nav-collapsed-bar1 {
        transform: rotate(-45deg) translate(-9px, 6px);
    }

    &__nav-collapsed--active &__nav-collapsed-bar2 {
        opacity: 0;
    }

    &__nav-collapsed--active &__nav-collapsed-bar3 {
        transform: rotate(45deg) translate(-8px, -8px);
    }
    

    @include media('small') {
        &__title {
            font-size: 20px;
        }
        &__description {
            font-size: 10px;
        }
        &__menu {
            display: none;
        }
        &__nav-collapsed {
            display: flex;
        }
    }

    @include media('medium') {
        &__title {
            font-size: 22px;
        }
        &__description {
            font-size: 12px;
        }
    }
}

// BACKGROUND HEADER

.background-header {
    height: 100vh;

    &__container {
        display: flex;
        align-items: center;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__title {
        @include heading();
        width: 100%;
        margin: 0;
        padding: 0 15%;
        font-size: 50px;
        font-weight: 700;
        letter-spacing: 1.5px;
        color: $white;
        text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.8);
    }

    &__whatsapp {
        display: flex;
        align-items: center;
        position: absolute;
        bottom: 10%;
        right: 50%;
        padding: 15px;
        background-color: #00e575;
        font-family: sans-serif;
        font-size: 20px;
        text-shadow: 1px 1px 5px black;
        text-decoration: none;
        transform: translateX(50%);
        color: white;
        border-radius: 10px;

        &:hover {
            background-color: #02c567;
        }
    }

    &__whatsapp-icon {
        font-size: 1.5em;
        margin-left: 5px;
    }

    @include media('small') {
        &__title {
            padding: 0 5%;
            font-size: 25px;
        }
    }

    @include media('medium') {
        &__title {
            padding: 0 10%;
            font-size: 35px;
        }
    }
}

// SERVICES

.services {
    &__container {
        @include container();
    }

    &__title {
        @include heading();
    }

    &__content {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-row-gap: 30px;
        justify-items: center;
    }

    &__item {
        position: relative;
        overflow: hidden;
        height: 280px;
        width: 280px;
        box-shadow: 4px 4px 10px 1px rgba(0,0,0,0.75);
    }

    &__item-img {
        position: absolute;
        height: 100%;
        right: 50%;
        transform: translateX(50%);
    }

    &__item-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        width: 100%;
        bottom: 0;
        padding: 10px;
        font-size: 18px;
        font-weight: 600;
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
        text-transform: uppercase;
        box-sizing: border-box;
    }

    &__item-icon {
        font-size: 18px;
    }

    @include media('small') {
        &__content {
            grid-template-columns: 1fr;
        }
    }

    @include media('medium') {
        &__content {
            grid-template-columns: 1fr 1fr;
        }
    }

    @include media('large') {
        &__content {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }
}

// FOOTER

.footer {
    background-color: $secondary;
    color: $white;

    &__container {
        @include container();
    }

    &__title {
        @include heading();
    }

    &__content {
        text-align: center;
    }

    &__information {
        text-align: center;
    }

    &__phone,
    &__address {
        font-family: sans-serif;
    }

    &__social-media {
        display: flex;
        justify-content: center;

        * {
            margin: 10px 8px 0 8px;
            font-size: 45px;
            color: $white;
            text-decoration: none;

            &:hover {
                color: $primary;
            }
        }
    }
}
